import { CangjieInputEvent, Controller, environment } from '@ali/4ever-cangjie';
import { createSpaceHandler } from '@ali/4ever-utils';

import { DELIMITER } from '../constants';

export default function createOnCangjieInput() {
  const onSpace = createSpaceHandler({
    mark: 'underline',
    delimiter: DELIMITER,
  });
  return function onCangjieInput(
    event: CangjieInputEvent,
    controller: Controller,
    next: Function,
  ) {
    if (!onSpace || event.detail.data !== ' ') return next();
    return onSpace(event, controller, next);
  };
}
